.rowser {
  display: flex;
  border-radius: 5px;
  align-items: center;
  justify-content: center;
  background-color: white;
  padding: 20px;
}
.contserv {
  background-color: white;
  padding: 5px 20px 15px 20px;
  border: none;
  width: 60%;

  border-radius: 3px;
}
.rw {
  display: -ms-flexbox; /* IE10 */
  display: flex;
  -ms-flex-wrap: wrap; /* IE10 */
  flex-wrap: wrap;
  gap: 10px;
}
.rmob {
  display: none;
}

@media screen and (max-width: 920px) {
  .contserv {
    width: 100%;
  }
  .rw {
    display: none;
  }
  .rmob {
    display: block;
  }
}
