.head {
  overflow: hidden;
  background-color: white;
  padding: 0px 25px;
  margin-top: 15px;
  border: 1px;
}
hr.solid {
  border-top: 1px solid #f2f2f2;
}

/* Style the head links */
.head a {
  float: left;
  color: black;
  text-align: center;
  margin-top: 4px;
  padding: 10px;
  text-decoration: none;
  font-size: 15px;
  line-height: 15px;
  border-radius: 4px;
}

/* Style the logo link (notice that we set the same value of line-height and font-size to prevent the head to increase when the font gets bigger */
.head a.logo {
  font-size: 25px;
  font-weight: bold;
}

/* Change the background color on mouse-over */
.head a:hover {
  background-color: #ddd;
  color: black;
}

/* Style the active/current link*/
.head a.active {
  background-color: #e23744;
  color: white;
}

/* Float the link section to the right */
.head-right {
  float: right;
}

/* Add media queries for responsiveness - when the screen is 500px wide or less, stack the links on top of each other */
@media screen and (max-width: 500px) {
  .head a {
    float: none;
    display: block;
    text-align: left;
  }
  .head-right {
    float: none;
  }
}
