body {
  background-color: white;
}

.header {
  overflow: hidden;
  padding: 15px 100px;
  background-color: white;
}

/* Style the header links */
.header a {
  float: left;
  color: black;
  text-align: center;
  margin-top: 4px;
  padding: 10px;
  text-decoration: none;
  font-size: 15px;
  line-height: 15px;
  border-radius: 4px;
}

.main-image {
  height: 50px;
  width: 150px;
  object-fit: contain;
}

/* Change the background color on mouse-over */
.header a:hover {
  background-color: #ddd;
  color: black;
  cursor: pointer;
}

/* Style the active/current link*/
.header a.activeh {
  background-color: #e23744;
  color: white;
}

/* Float the link section to the right */
.header-right {
  float: right;
}

.btn {
  border: 2px solid black;
  border-radius: 20px;
  background-color: white;
  color: black;
  font-family: "poppins";
  padding: 9px 14px;
  font-size: 13px;
  cursor: pointer;
}
.default {
  border-color: #e7e7e7;
  color: black;
  float: left;
  margin-right: 2rem;
}

.default:hover {
  background: #e7e7e7;
}
.icon-ioc {
  margin-right: 3px;
  vertical-align: middle;
  margin-bottom: 3px;
}

.hero {
  position: relative;
  width: 100%;

  height: 450px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero::before {
  content: "";

  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("../../assests/title.png");
  background-color: #301934;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  filter: brightness(30%);
}
.hero-content {
  position: relative;
  bottom: 5%;
  font-family: "poppins";
  line-height: 25px;

  color: white;
  text-align: center;
  margin: 0.625rem;
}
.hero-title {
  font-size: 3rem;
  font-weight: 300;

  text-align: center;
}
.hero-subtitle {
  font-size: 2rem;
  font-weight: 200;
}

.hero-btn {
  border-radius: 20px;
  background-color: #e23744;
  color: white;
  padding: 10px 14px;
  font-size: 13px;
  margin-top: 10px;
  cursor: pointer;
  font-family: "Poppins";

  border: 1px solid #cb376a;
}
.hero-btn:hover {
  opacity: 0.8;
}
.explore {
  display: flex;
  font-size: 15px;
  justify-content: center;
  text-align: center;
  align-items: center;
  font-family: "Poppins";
  margin-top: 2rem;
}
.card {
  display: flex;
  justify-content: center;
  cursor: pointer;
  align-items: center;
  gap: 2rem;
}
.card-container {
  width: 200px;
  height: 170px;
  border-radius: 20px;
  overflow: hidden;
  margin: 1em;
  box-shadow: 0px 0px 20px -12px;
  font-family: "poppins";
  font-size: 14px;
}

.card-img {
  overflow: hidden;
  height: 80px;
  transition: 0.5s;
  animation: ease-in-out;
}
.card-img:hover {
  transform: scale(1.1);
}

.card-content {
  text-align: center;
  margin-top: 1rem;
}
.modal {
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */

  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
  background-color: #fefefe;

  margin: 15% auto; /* 15% from the top and centered */
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 30px;
  border: 1px solid #888;
  width: 55%; /* Could be more or less, depending on screen size */
}

/* The Close Button */
.close {
  color: #aaa;
  float: right;
  line-height: 0px;
  padding: 20px;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
form {
  align-items: center;
  justify-content: center;
}
.row {
  display: flex;
  width: 100%;
}

/* Full-width inputs */
input[type="text"] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  font-family: "poppins";

  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}
input[type="submit"] {
  width: 20%;
  padding: 12px 20px;
  margin: 8px 0;
  font-family: "poppins";

  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}
.logbtn {
  background-color: #e23744;
  color: white;
  padding: 14px 20px;
  font-family: "poppins";
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
}
/* Add a hover effect for buttons */
.logbtn:hover {
  opacity: 0.8;
}
.logtxt {
  font-size: 25px;
  line-height: 0px;
  font-family: "poppins";
}

.sidebar {
  height: 100%; /* 100% Full-height */
  width: 0; /* 0 width - change this with JavaScript */
  position: fixed; /* Stay in place */
  z-index: 1; /* Stay on top */
  top: 0;
  left: 0;
  overflow: hidden;
  transition: 0.5s; /* 0.5 second transition effect to slide in the sidebar */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}
.sidebar-content {
  background: white;
  height: 100%;
  padding-top: 60px;
  width: 500px;
  overflow-y: scroll;
}
.closebtn {
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}
.wrap {
  width: 80%;
  padding-left: 20px;
}
.loct {
  width: 99%;
  margin-top: 10px;
  border: 2px solid black;
  border-color: #e7e7e7;
  background-color: white;
  border-radius: 0px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.btngps {
  color: #e23744;
  background-color: #fff;
  border: none;
  font-family: "poppins";
  font-size: 13px;
  cursor: pointer;
}

.unique-txt {
  font-size: 20px;
  font-family: "poppins";
}
.uniquemob {
  display: none;
}

/* Slideshow container */
.slideshow {
  position: relative;
}

.slideshow::before {
  content: "";

  width: 100%;
  height: 100%;
  position: absolute;
  background: url("../../assests/title.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  filter: brightness(20%);
}

/* Slides */
.mySlides {
  display: none;
  position: relative;
  color: white;
  padding: 80px;
  text-align: center;
  font-family: "poppins";
}

/* Next & previous buttons */
.prev,
.next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -30px;
  padding: 16px;
  color: #888;
  font-weight: bold;
  font-size: 20px;
  border-radius: 0 3px 3px 0;
  user-select: none;
}

/* Position the "next button" to the right */
.next {
  position: absolute;
  right: 0;
  border-radius: 3px 0 0 3px;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover,
.next:hover {
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
}

/* The dot/bullet/indicator container */
.dot-container {
  text-align: center;
  padding: 20px;
  background: whitesmoke;
}

/* The dots/bullets/indicators */
.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

/* Add a background color to the active dot/circle */
.active,
.dot:hover {
  background-color: #ff9f55;
}

/* Add an italic font style to all quotes */
q {
  font-style: italic;
}
.video {
  display: flex;
}
.viframe {
  width: 520px;
  height: 320px;
}

.coframe {
  position: relative;
  overflow: hidden;
  width: 100%;

  padding-top: 56.25%; /* 16:9 Aspect Ratio (divide 9 by 16 = 0.5625) */
}
.comframe {
  position: relative;
  overflow: hidden;
  width: 90%;
  padding-top: 56.25%;
}

/* Then style the iframe to fit in the container div with full height and width */
.responsive-iframe {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.ytcontent {
  width: 500px;
}
.ipad {
  display: none;
}

.collapsible {
  background-color: #eee;
  color: #444;
  cursor: pointer;
  padding: 18px;
  width: 80%;
  font-family: "poppins";

  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
}

/* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
.activefaq,
.collapsible:hover {
  background-color: #ccc;
}

/* Style the collapsible content. Note: hidden by default */
.collaspe {
  padding: 0 18px;
  background-color: #eee;
  max-height: 0;
  font-family: "poppins";
  overflow: hidden;
  transition: max-height 0.2s ease-out;
}
.collapsible:after {
  content: "\02795"; /* Unicode character for "plus" sign (+) */
  font-size: 13px;
  color: white;
  float: right;
  margin-left: 5px;
}

.activefaq:after {
  content: "\2796"; /* Unicode character for "minus" sign (-) */
}

.app {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  font-family: "poppins";
  gap: 5rem;
}
.atsuggesttxt {
  color: black;
  line-height: 15px;
}
.atsuggesttxt:hover {
  color: #e23744;
}

.modalimg {
  /* width: "100%",
  maxWidth: "400px", */
  object-fit: contain;
  width: 100%;
  max-width: 400px;
}

/* Add media queries for responsiveness - when the screen is 560px wide or less, stack the links on top of each other */
@media screen and (max-width: 560px) {
  .header {
    overflow: hidden;
    background-color: #ffffff;
    padding: 20px 10px;
  }
  .main-image {
    height: 30px;
    width: 130px;
    margin-top: 7px;
    object-fit: contain;
  }

  .hero-content {
    font-family: "Poppins";
    line-height: normal;
    color: white;
    text-align: center;
    margin: 0.625rem;
  }
  .hero-title {
    font-size: 20px;

    font-weight: 300;
    text-align: center;
  }
  .hero-subtitle {
    font-size: 25px;
    font-weight: 200;
  }
  .explore {
    display: flex;
    font-size: 13px;
    justify-content: center;
    text-align: center;
    align-items: center;
    font-family: "Poppins";
    margin-top: 1rem;
  }
  .card {
    display: flex;
    justify-content: center;
    cursor: pointer;
    align-items: center;
    gap: 0;
  }
  .modal-content {
    background-color: #fefefe;

    margin: 15% auto; /* 15% from the top and centered */
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 20px;
    border: 1px solid #888;
    width: 90%; /* Could be more or less, depending on screen size */
  }
  .sidebar-content {
    background: white;
    height: 100%;
    padding-top: 60px;
    width: 70%;
  }
  .timeline {
    display: block;
    justify-content: center;
    cursor: pointer;
    align-items: center;
  }
  .uniquemob {
    display: flex;
    font-family: "poppins";
    align-items: center;
    justify-content: center;
  }
  .viframe {
    display: none;
  }
  .app {
    flex-direction: column;
    gap: 0;
  }
  .modalimg {
    display: none;
  }
}

@media screen and (max-width: 912px) {
  .modal-content {
    background-color: #fefefe;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 20px;
    margin: 15% auto;
    border: 1px solid #888;
    width: 90%;
  }
  .ytcontent {
    display: none;
  }
  .ipad {
    display: block;
  }
}
