* {
  box-sizing: border-box;
}

/* Style inputs */
.input,
select,
textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}

.inputsubmit {
  background-color: #04aa6d;
  color: white;
  padding: 12px 20px;
  border: none;
  cursor: pointer;
}

.inputsubmit:hover {
  background-color: #45a049;
}

/* Style the container/contact section */
.container {
  border-radius: 5px;
  background-color: white;
  padding: 10px;
}

/* Create two columns that float next to eachother */
.column {
  float: left;
  width: 50%;
  margin-top: 6px;

  padding: 20px;
}

.col {
  float: left;
  display: flex;
  width: 50%;
  justify-content: center;
  font-family: "poppins";
  margin-top: 6px;
  padding: 20px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}
.vl {
  border-left: 6px solid #f2f2f2;
  height: 500px;
  position: absolute;
  left: 54%;
  top: 50;
}
.memail {
  display: none;
}
.ficon {
  display: flex;
  gap: 12px;
  transition: 0.3s;
  cursor: pointer;
}
.ficon :hover {
  transform: translate(0, -3px);
}
.fbox {
  display: none;
}

/* Responsive layout - when the screen is less than 600px wide, make the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .column,
  input[type="submit"] {
    width: 100%;
    margin-top: 0;
  }
  .col {
    display: none;
  }
  .memail {
    display: block;
    text-align: center;
    font-family: "poppins";
    align-items: center;
    justify-content: center;
  }
  .fbox {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
