.footer {
  background: #151414;
}
.footer-content {
  display: flex;
  flex-direction: row;
  align-items: center;

  justify-content: space-evenly;
}
.footer-top {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  color: white;
  line-height: 10px;
  font-family: "poppins";
}
.divider {
  border-color: #757575;

  margin-top: 10px;
}
@media screen and (max-width: 560px) {
  .footer-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
